import * as React from "react";
import { Link, graphql } from "gatsby";
import Layout from "../../components/layout";
import BlogLink from "../../components/blog-link";

const BlogPage = ({ data }) => {
  const content = data.allMdx.nodes.filter(
    (item) => item.frontmatter.title !== "Aboutme"
  );

  return (
    <Layout pageTitle="My Blog Posts">
      {content.map((node) => (
        <Link style={{ padding: "0px" }} to={`/blog/${node.slug}`}>
          <BlogLink
            key={node.id}
            title={node.frontmatter.title}
            datePublished={node.frontmatter.datePublished}
          />
        </Link>
      ))}
    </Layout>
  );
};

export const query = graphql`
  query {
    allMdx(sort: { fields: frontmatter___datePublished, order: DESC }) {
      nodes {
        frontmatter {
          datePublished(formatString: "D MMMM YYYY", locale: "tr")
          author
          title
        }
        id
        slug
      }
    }
  }
`;

export default BlogPage;
